import React from 'react';


var style = {
    backgroundColor: "#F8F8F8",
    borderTop: "1px solid #E7E7E7",
    textAlign: "center",
    padding: "20px",
    position: "fixed",
    left: "0",
    bottom: "0",
    height: "60px",
    width: "100%",
}

var phantom = {
  display: 'block',
  padding: '20px',
  height: '60px',
  width: '100%',
}

export default class HomePage extends React.Component {	 
  render() {    
    return (
	 <div>
		<div>
			<p>The Shapes contained on this webPage are listed in AISC, SSMA, IS Steel 
				Construction Manual
			</p>
	  
			
		
		</div>
		<div>
            <div style={phantom} />
            <div style={style}>
                 <p>Disclaimer: 
					The information presented on this web page is for general information only.
					Use of this information is for the educational purpose only. ...
				 </p>
					Links:  
						America Institute of Steel Construction AISC: Steel Dimension
						https://www.aisc.org/publications/detailing-resources2/dimensioningtool/

						Steel Stud Manufacturing Association SSAMA
						https://ssma.com/
            </div>
        </div>

	 </div>


	)

  }
}
