/*
How to deploy React app on GitHub
1) create a new repository on GitHub say "LawOffice" keep it public
2) npm install gh-pages --save 
3) open package.json and put a new line on the top as follows
	  "homepage":"http://GitHubUserName.github.io/LawOffice", 
4) add two new line under block "Scripts" :{
		"predeploy":"npm run build",
		"deploy":"gh-pages -d build",

5) >git init 
6) >git remote add origin https://github.com/GitUserName/LawOffice.GitHub
7) > git status
8) > git add .
9) > git commint -m "version-1_description"
10) npm run deploy
11) git push -u origin master   //this will push code to GitHub
12) go to GitHub and go inside folder LawOffice and to setting, and get the publish url from there...

*/


//import './App.css';
import React from 'react';
import * as d3 from 'd3';
import {Button, ButtonGroup,Table } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';

class ISectionProp extends React.Component {
  constructor(props){
  	  super(props);	
		  this.state={
	  		  csvArrayData:[],
			  sectionName:"",
			  sectionType:"",
			  spaceVar:" ::: ",
		  };
	  this.handleClickButtonGroupIsection = this.handleClickButtonGroupIsection.bind(this);
	 // this.handleClickButtonGroupCsection = this.handleClickButtonGroupCsection.bind(this);
	 // this.handleClickButtonGroupTsection = this.handleClickButtonGroupTsection.bind(this);
	 // this.handleClickButtonGroupAnglesection = this.handleClickButtonGroupAnglesection.bind(this);
	  this.handleClickButtonSectionType = this.handleClickButtonSectionType.bind(this);
  };
  
componentDidMount() {
    d3.csv('i_section.csv')
		.then( (data)=>{
			console.log(data);
			this.setState({csvArrayData: data});
		})
		.catch(function(err) {
			throw err;
    })
}

  handleClickButtonSectionType(e){
	this.setState({sectionType: e.target.value})
  }
  handleClickButtonGroupIsection(e){
    //console.log(e.target.value);
	this.setState({sectionName: e.target.value})
  };
      
  render() {
     var tableHeaderRow = <tr>
					<th>Designation</th> <th>Weight (w) kg/m</th> 
					<th>Area cm2</th> <th>Height mm</th> 	
					<th>Width mm</th>  <th>Flange Thk mm</th>
					<th>Web Thk mm</th> <th>Ixx_cm4</th> 
					<th>Iyy_cm4</th> <th>Rxx_cm</th> 
					<th>Ryy_cm</th> <th>Zxx_cm3</th> 
					<th>Zyy_cm3</th>
				</tr>
   	
	var sectionButtonGroup=null;
	if(this.state.sectionType ==="I-Section"){
		sectionButtonGroup= <ButtonGroup onClick={this.handleClickButtonGroupIsection}>
									<Button variant="dark" value="ISJB">ISJB</Button>
									<Button variant="secondary" value="ISLB">ISLB</Button>
									<Button variant="dark" value="ISMB">ISMB</Button>
									<Button variant="dark" value="ISWB">ISWB</Button>
									<Button variant="secondary" value="ISHB">ISHB</Button>
							</ButtonGroup>
							 
	};
	
	var  tableBodyObj=null;  
	//console.log(csvArrayData);
	if(this.state.sectionName===""){
		//console.log(this.state.csvArrayData);
		tableBodyObj = this.state.csvArrayData.map( (dataRow, index) => (
			<tr key={index}>								
				<td>{dataRow.Section}</td> <td>{dataRow.w_kg}</td>
				<td>{dataRow.a_cm2}</td> <td>{dataRow.h_mm}</td>
				<td>{dataRow.b_mm}</td> <td>{dataRow.tf_mm}</td>
				<td>{dataRow.tw_mm}</td> <td>{dataRow.Ixx_cm4}</td>
				<td>{dataRow.Iyy_cm4}</td> <td>{dataRow.rxx_cm}</td> 
				<td>{dataRow.ryy_cm}</td> 	<td>{dataRow.Zxx_cm3}</td>
				<td>{dataRow.Zyy_cm3}</td>	
			</tr>
		));
	}
	else{		 
		var secNameString= this.state.sectionName;
		const { csvArrayData } = this.state;
		var resultFilter = csvArrayData.filter(function (element) {
			  return element.Section.includes(secNameString); 
		});
		tableBodyObj = resultFilter.map( (dataRow, index) => (
			<tr key={index}>								
				<td>{dataRow.Section}</td> <td>{dataRow.w_kg}</td>
				<td>{dataRow.a_cm2}</td> <td>{dataRow.h_mm}</td>
				<td>{dataRow.b_mm}</td> <td>{dataRow.tf_mm}</td>
				<td>{dataRow.tw_mm}</td> <td>{dataRow.Ixx_cm4}</td>
				<td>{dataRow.Iyy_cm4}</td> <td>{dataRow.rxx_cm}</td> 
				<td>{dataRow.ryy_cm}</td> 	<td>{dataRow.Zxx_cm3}</td>
				<td>{dataRow.Zyy_cm3}</td>	
			</tr>
		));	
	}
	
	return ( 
		<div>
				<div className='rows'> 
					<ButtonGroup onClick={this.handleClickButtonSectionType}>
					<Button variant="dark" value="I-Section">I-Section : </Button>
					</ButtonGroup> 
					{ this.state.spaceVar }
					{ sectionButtonGroup }
				</div>

				<div>
				<Table striped bordered hover>
					<thead>
						{ tableHeaderRow }
					</thead>
						<tbody>
							{ tableBodyObj }
						</tbody>
				</Table>
				</div> 
			</div>
    );
  }

}
export default ISectionProp;
