import React from 'react';
import * as d3 from 'd3';
import {Button, ButtonGroup,Table } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';

class TSectionProp extends React.Component {
  constructor(props){
  	  super(props);	
		  this.state={
	  		  csvArrayData:[],
			  sectionName:"",
			  sectionType:"",
			  spaceVar:" ::: ",
		  };
	  this.handleClickButtonGroupIsection = this.handleClickButtonGroupIsection.bind(this);
	 // this.handleClickButtonGroupCsection = this.handleClickButtonGroupCsection.bind(this);
	 // this.handleClickButtonGroupTsection = this.handleClickButtonGroupTsection.bind(this);
	 // this.handleClickButtonGroupAnglesection = this.handleClickButtonGroupAnglesection.bind(this);
	  this.handleClickButtonSectionType = this.handleClickButtonSectionType.bind(this);
  };
  
componentDidMount() {
    d3.csv('t_section.csv')
		.then( (data)=>{
			//console.log(data);
			this.setState({csvArrayData: data});
		})
		.catch(function(err) {
			throw err;
    })
}

  handleClickButtonSectionType(e){
	this.setState({sectionType: e.target.value})
  }
  handleClickButtonGroupIsection(e){
    console.log(e.target.value);
	this.setState({sectionName: e.target.value})
  };
      
  render() {
  
  var tableHeaderRow = <tr>
					<th>Section</th> <th>Thickness </th> <th>Area cm2</th>
					<th>Depth</th> <th>FlangeWidth</th>  
					<th>FlangeThk</th> <th>WebThk</th> <th>CG_Cyy</th> 
					<th>Ixx</th> <th> Iyy</th>  <th>Rxx</th> <th>Ryy</th>
					<th>Zxx</th> <th>Zyy</th> 
				</tr>
   	
	//var sectionButtonGroup=null;
/*
	if(this.state.sectionType ==="T-Section"){
		sectionButtonGroup= <ButtonGroup onClick={this.handleClickButtonGroupIsection}>
				<Button variant="dark" value="ISNT">ISNT</Button>
				<Button variant="secondary" value="ISST">ISST</Button>
				<Button variant="dark" value="ISLT">ISLT</Button>
				<Button variant="dark" value="ISJT">ISJT</Button>
		</ButtonGroup>							 
	};
*/	
	var  tableBodyObj=null;  
	//console.log(csvArrayData);
	if(this.state.sectionName===""){
		console.log(this.state.csvArrayData);
		tableBodyObj = this.state.csvArrayData.map( (dataRow, index) => (
			<tr key={index}>								
				<td>{dataRow.Section}</td> <td>{dataRow.Wt}</td>
				<td>{dataRow.Area}</td> <td>{dataRow.Depth}</td>
				<td>{dataRow.FlangeWidth}</td> <td>{dataRow.FlangeThk}</td>
				<td>{dataRow.WebThk}</td> 	<td>{dataRow.CG_Cyy}</td> 
				<td>{dataRow.Ixx}</td> 	<td>{dataRow.Iyy}</td>
				<td>{dataRow.Rxx}</td> <td>{dataRow.Ryy}</td>
				<td>{dataRow.Zxx}</td> <td>{dataRow.Zyy}</td>	
			</tr>
		));
	}
/*	else{		 
		var secNameString= this.state.sectionName;
		const { csvArrayData } = this.state;
		var resultFilter = csvArrayData.filter(function (element) {
			  return element.Section.includes(secNameString); 
		});
		tableBodyObj = resultFilter.map( (dataRow, index) => (
			<tr key={index}>								
				<td>{dataRow.Section}</td> <td>{dataRow.Wt}</td>
				<td>{dataRow.Area}</td> <td>{dataRow.Depth}</td>
				<td>{dataRow.FlangeWidth}</td> <td>{dataRow.FlangeThk}</td>
				<td>{dataRow.WebThickness}</td> <td>{dataRow.CG_Cyy}</td>
				<td>{dataRow.Ixx}</td> 	<td>{dataRow.Iyy}</td> 
				<td>{dataRow.Rxx}</td> 	<td>{dataRow.Ryy}</td>
				<td>{dataRow.Zxx}</td> <td>{dataRow.Zyy}</td>	
			</tr>
		));	
	}
	
*/
	return ( 
		<div>
				<div className='rows'> 
					<ButtonGroup onClick={this.handleClickButtonSectionType}>
					<Button variant="dark" value="T-Section">T-Section </Button>
					</ButtonGroup> 					 
				</div>

				<div>
				<Table striped bordered hover>
					<thead>
						{ tableHeaderRow }
					</thead>
						<tbody>
							{ tableBodyObj }
						</tbody>
				</Table>
				</div> 
			</div>
    );
  }

}
export default TSectionProp;
