import React from 'react';
import * as d3 from 'd3';
import {Button, ButtonGroup,Table } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';

class CSectionProp extends React.Component {
  constructor(props){
  	  super(props);	
		  this.state={
	  		  csvArrayData:[],
			  sectionName:"",
			  sectionType:"",
			  spaceVar:" ::: ",
		  };
	  //this.handleClickButtonGroupIsection = this.handleClickButtonGroupIsection.bind(this);
	  this.handleClickButtonGroupCsection = this.handleClickButtonGroupCsection.bind(this);
	  //this.handleClickButtonGroupTsection = this.handleClickButtonGroupTsection.bind(this);
	  //this.handleClickButtonGroupAnglesection = this.handleClickButtonGroupAnglesection.bind(this);
	  this.handleClickButtonSectionType = this.handleClickButtonSectionType.bind(this);
  };
  
componentDidMount() {
    d3.csv('ssma_stud_track.csv')
		.then( (data)=>{
			//console.log(data);
			this.setState({csvArrayData: data});
		})
		.catch(function(err) {
			throw err;
    })
}

  handleClickButtonSectionType(e){
	this.setState({sectionType: e.target.value})
  };
   
  handleClickButtonGroupCsection(e){
    console.log(e.target.value);
	this.setState({sectionName: e.target.value})
  };
     
  //Section				Gross						33 ksi Effective						50 kis Effective					Tortional


  render() {
   	var tableHeaderRow1 =	<tr>
								<th colspan="9">Gross Sectional Properties</th>
								<th colspan="5">33 KSI Effective Properties</th>
								<th colspan="5">55 KSI Effective Properties</th>
								<th colspan="5">Tortional Properties</th>
							</tr>

	var tableHeaderRow2 =	<tr>
								<th> Section  </th>  <th> Thk  </th>
								<th> Area_in2  </th> <th> Weight_lb_ft  </th>
								<th> Ixx_in4   </th> <th> Sxx_in3   </th>
								<th> Rx_in   </th> <th> Iyy_in4   </th> 
								<th> Ry_in  </th> 
			
								<th> Ixx_in4   </th> 
								<th> Sxx_in3   </th> 	<th> Ma_in_k   </th>
								<th> Va_lb_   </th> 	<th> Ycg_in   </th>

								<th> Ixx_in4   </th> <th> Sxx_in3  </th>
								<th> Ma_in_k   </th> <th> Va_lb   </th>
								<th> Ycg_in   </th> 
			
								<th> Jx1000_in4  </th>
								<th> Cw_in6 </th><th> Xo </th>
								<th> Ro</th> 
								<th> beta</th>
							</tr>


//Strut / Track Size: 162, 250, 350, 362, 400, 550, 600, 725, 800, 925, 1000, 1150, 1200, 1350, 1400, 1600
	var sectionButtonGroup=null;
	if(this.state.sectionType ==="stud-section"){
		sectionButtonGroup= <ButtonGroup onClick={this.handleClickButtonGroupCsection}>
				<Button variant="dark" value="162S">162 S</Button>
				<Button variant="secondary" value="250S">250 S</Button>
				<Button variant="dark" value="350S">250 S</Button>
				<Button variant="secondary" value="362S">362 S</Button>

				<Button variant="dark" value="400S">400 S</Button>
				<Button variant="secondary" value="550S">550 S</Button>
				<Button variant="dark" value="600S">600 S</Button>
				<Button variant="secondary" value="725S">725 S</Button>

				<Button variant="dark" value="800S">800 S</Button>
				<Button variant="secondary" value="925S">925 S</Button>
				<Button variant="dark" value="1000S">1000 S</Button>
				<Button variant="secondary" value="1150S">1150 S</Button>

				<Button variant="dark" value="1200S">1200 S</Button>
				<Button variant="secondary" value="1350S">1350 S</Button>
				<Button variant="dark" value="1400S">1400 S</Button>
				<Button variant="secondary" value="1600S">1600 S</Button>
		</ButtonGroup>							 
	}
	else{
		if(this.state.sectionType ==="track-section"){
			sectionButtonGroup= <ButtonGroup onClick={this.handleClickButtonGroupCsection}>
				<Button variant="dark" value="162T">162 T</Button>
				<Button variant="secondary" value="250T">250 T</Button>
				<Button variant="dark" value="350T">250 T</Button>
				<Button variant="secondary" value="362T">362 T</Button>

				<Button variant="dark" value="400T">400 T</Button>
				<Button variant="secondary" value="550T">550 T</Button>
				<Button variant="dark" value="600T">600 T</Button>
				<Button variant="secondary" value="725T">725 T</Button>

				<Button variant="dark" value="800T">800 T</Button>
				<Button variant="secondary" value="925T">925 T</Button>
				<Button variant="dark" value="1000T">1000 T</Button>
				<Button variant="secondary" value="1150T">1150 T</Button>

				<Button variant="dark" value="1200T">1200 T</Button>
				<Button variant="secondary" value="1350T">1350 T</Button>
				<Button variant="dark" value="1400T">1400 T</Button>
				<Button variant="secondary" value="1600T">1600 T</Button>
			</ButtonGroup>							 
		}
	};


	var  tableBodyObj=null;  
	//console.log(csvArrayData);
	if(this.state.sectionName===""){
		console.log(this.state.csvArrayData);

		tableBodyObj = this.state.csvArrayData.map( (dataRow, index) => (
			<tr key={index}>								
				<td>{dataRow.Section}</td>  <td>{dataRow.Thk_in_gross}</td>
				<td>{dataRow.Area_in2Thk_in_gross}</td> <td>{dataRow.Weight_lb_ftThk_in_gross}</td>
				<td>{dataRow.Ixx_in4Thk_in_gross}</td> <td>{dataRow.Sxx_in3Thk_in_gross}</td>
				<td>{dataRow.Rx_inThk_in_gross}</td> <td>{dataRow.Iyy_in4Thk_in_gross}</td> 
				<td>{dataRow.Ry_inThk_in_gross}</td> <td>{dataRow.Ixx_in4_33ksi}</td> 
				<td>{dataRow.Sxx_in3_33ksi}</td> 	<td>{dataRow.Ma_in_k_33ksi}</td>
				<td>{dataRow.Va_lb_33ksi}</td> 	<td>{dataRow.Ycg_in_33ksi}</td>
				<td>{dataRow.Ixx_in4_50ksi}</td> <td>{dataRow.Sxx_in3_50ksi}</td>
				<td>{dataRow.Ma_in_k_50ksi}</td> <td>{dataRow.Va_lb_50ksi}</td>
				<td>{dataRow.Ycg_in_50ksi}</td> <td>{dataRow.Jx1000_in4_tor}</td>
				<td>{dataRow.Cw_in6_tor}</td><td>{dataRow.Xo_in_tor}</td>
				<td>{dataRow.Ro_in_tor}</td> <td>{dataRow.beta_tor}</td>		
			</tr>
		));
	}
	else{		 
		var secNameString= this.state.sectionName;
		const { csvArrayData } = this.state;
		var resultFilter = csvArrayData.filter(function (element) {
			  return element.Section.includes(secNameString); 
		});

		tableBodyObj = resultFilter.map( (dataRow, index) => (
			<tr key={index}>								
				<td>{dataRow.Section}</td>  <td>{dataRow.Thk_in_gross}</td>
				<td>{dataRow.Area_in2Thk_in_gross}</td> <td>{dataRow.Weight_lb_ftThk_in_gross}</td>
				<td>{dataRow.Ixx_in4Thk_in_gross}</td> <td>{dataRow.Sxx_in3Thk_in_gross}</td>
				<td>{dataRow.Rx_inThk_in_gross}</td> <td>{dataRow.Iyy_in4Thk_in_gross}</td> 
				<td>{dataRow.Ry_inThk_in_gross}</td> <td>{dataRow.Ixx_in4_33ksi}</td> 
				<td>{dataRow.Sxx_in3_33ksi}</td> 	<td>{dataRow.Ma_in_k_33ksi}</td>
				<td>{dataRow.Va_lb_33ksi}</td> 	<td>{dataRow.Ycg_in_33ksi}</td>
				<td>{dataRow.Ixx_in4_50ksi}</td> <td>{dataRow.Sxx_in3_50ksi}</td>
				<td>{dataRow.Ma_in_k_50ksi}</td> <td>{dataRow.Va_lb_50ksi}</td>
				<td>{dataRow.Ycg_in_50ksi}</td> <td>{dataRow.Jx1000_in4_tor}</td>
				<td>{dataRow.Cw_in6_tor}</td><td>{dataRow.Xo_in_tor}</td>
				<td>{dataRow.Ro_in_tor}</td> <td>{dataRow.beta_tor}</td>		
			</tr>

		));	
	}
	

    return ( 
             <div>
				  <div className='rows'> 
					 <ButtonGroup onClick={this.handleClickButtonSectionType}>
						<Button variant="dark" value="stud-section">Stud-Sections : </Button>
						<Button variant="secondary" value="track-section">Track-Sections : </Button>
					 </ButtonGroup> 
						{this.state.spaceVar}
						{sectionButtonGroup}
				   </div>

				
					<div>
					<Table striped bordered hover>
						<thead>
							{ tableHeaderRow1 }	
							{ tableHeaderRow2 }	
						  </thead>

							<tbody>
								{ tableBodyObj }
							</tbody>
	
					</Table>
				  </div> 
				  
             </div>
    );
  }

}
export default CSectionProp;
