/*
How to deploy React app on GitHub
1) create a new repository on GitHub say "webSteelDesign" keep it public
2) npm install --save gh-pages 
3) open package.json and put a new line on the top of file, as follows
	  "homepage": "http://lgframe.github.io/steeldesign", 
4) add two new line under block "Scripts" :{
		"predeploy":"npm run build",
		"deploy":"gh-pages -d build"
		/////// to remove git origin, > use  git remote remove origin
5) >git init 
6) >git remote add origin https://github.com/lgframe/steeldesign.git
11) > npm run deploy
12) go to GitHub and go inside folder webSteelDesign and to setting, 
    and get the publish url from there...

*/

import React from 'react';  //user BrowserRouter for app and HashRouter when deploy on GitHub
import { HashRouter as Router, Switch, Route, Link} from "react-router-dom";  //import { BrowserRouter as Router, Switch, Route} from "react-router-dom";
 import { Navbar,Nav,NavDropdown } from 'react-bootstrap'

import HomePage from './HomePage'

import AISCImperialSectionProp from'./asicImperialSectionProp'
import SSSMASectionProp from './SSSMASectionProp';
import ISectionProp from './ISectionProp';
import CSectionProp from './CSectionProp';
import TSectionProp from './TSectionProp';
import EqAngleSectionProp from './EqAngleSectionProp';
import UnEqAngleSectionProp from './UnEqAngleSectionProp';

export default class App extends React.Component {
 
  render(){
        return(
            <div>
                <div className="row">
                    <div className="col-md-12">
                        <Router>
                            <Navbar bg="dark" variant="dark" expand="lg" sticky="top">
                                <Navbar.Brand href="/">Fidelytix</Navbar.Brand>
                                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                                <Navbar.Collapse id="basic-navbar-nav">
                                    <Nav className="mr-auto">
                                    <Nav.Link as={Link} to="/aiscSteelSections">AISC Steel Sections</Nav.Link>
                                    <Nav.Link as={Link} to="/ssmaStrudTrackSections">SSMA Stud/Track Sections</Nav.Link>
										<NavDropdown title="IS-SteelSection" id="basic-nav-dropdown">
											<NavDropdown.Item as={Link} to="/isIsteelSection">I-Section</NavDropdown.Item>
											<NavDropdown.Item as={Link} to="/isCsteelSection">C-Section</NavDropdown.Item>
											<NavDropdown.Item as={Link} to="/isTsteelSection">T-Section</NavDropdown.Item>
											<NavDropdown.Divider />
											<NavDropdown.Item as={Link} to="/isEqAgnleSteelSection">Eq Angle-Section</NavDropdown.Item>
											<NavDropdown.Item has={Link} toref="/isUnEqAgnleSteelSection">UnEq Angle-Section action</NavDropdown.Item>
											<NavDropdown.Item as={Link} to="#action/3.4">Separated link</NavDropdown.Item>
										</NavDropdown>
                                    </Nav>
                                </Navbar.Collapse>
                            </Navbar>
                            <br />
                            <Switch>
                                <Route exact path="/"><HomePage />  </Route>
								<Route path="/aiscSteelSections">  <AISCImperialSectionProp />  </Route>
                                <Route path="/ssmaStrudTrackSections">  <SSSMASectionProp />  </Route>

								<Route path="/isIsteelSection">  <ISectionProp /> </Route>
								<Route path="/isCsteelSection">  <CSectionProp />  </Route>
								<Route path="/isTsteelSection">  <TSectionProp />  </Route>

								<Route path="/isEqAgnleSteelSection">  <EqAngleSectionProp />  </Route>
								<Route path="/isUnEqAgnleSteelSection">  <UnEqAngleSectionProp />  </Route>
                            </Switch>
                        </Router>
                    </div>
                </div>
            </div>
        )  
  }
}


