/*
How to deploy React app on GitHub
1) create a new repository on GitHub say "LawOffice" keep it public
2) npm install gh-pages --save 
3) open package.json and put a new line on the top as follows
	  "homepage":"http://GitHubUserName.github.io/LawOffice", 
4) add two new line under block "Scripts" :{
		"predeploy":"npm run build",
		"deploy":"gh-pages -d build",

5) >git init 
6) >git remote add origin https://github.com/GitUserName/LawOffice.GitHub
7) > git status
8) > git add .
9) > git commint -m "version-1_description"
10) npm run deploy
11) git push -u origin master   //this will push code to GitHub
12) go to GitHub and go inside folder LawOffice and to setting, and get the publish url from there...

*/


